.main{
  color: #fff;
  background: linear-gradient(360deg, rgba(20, 20, 20, 0.4) 0%, rgba(20, 20, 20, 0) 52.73%), linear-gradient(0deg, rgba(20, 20, 20, 0.5), rgba(20, 20, 20, 0.5)), url(../img/main-bg.webp);
  background-size: cover;
  background-position: center;
  margin-top: -89px;
  overflow: hidden;
  position: relative;
  &:before{

  }
  &__inner{
    padding: 210px 0 85px;
    position: relative;
    z-index: 2;
    display: grid;
    grid-template-columns: 240px auto;
    gap: 60px;
  }
  &__title{
    span{
      color: $primary;
    }
  }
  &__bottom{
    margin-top: 25px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__desc{
    max-width: 341px;
  }
  &__img{
    position: absolute;
    left: 0;
    bottom: 0;
  }
  &__btn{
    width: 201px;
    height: 201px;
    border: 2px solid #FFFFFF;
    box-shadow: 0px 0px 150px #C39F7B;
    border-radius: 50%;
    font-weight: 700;
    text-transform: uppercase;
    font-size: 18px;
  }
}

.advantages{
  margin-top: 60px;
  padding: 60px 0;
  position: relative;
  .container{
    position: relative;
    z-index: 2;
  }
  .blur-round{
    bottom: -170px;
    left: -190px;
  }
  &__title{
    span{
      color: $textColor;
      padding-left: 90px;
    }
  }
  &__items{
    margin-top: 60px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 30px;
  }
}

.facts{
  padding: 60px 0;
  position: relative;
  &__tree{
    position: absolute;
    top: 120px;
    right: 0;
    &--mob{
      display: none;
    }
  }
  &__grid{
    position: relative;
    z-index: 2;
    display: grid;
    grid-template-columns: 42.85% auto;
    gap: 30px 7.5%;
  }
  &__top{
    align-items: center;
  }
  &__title{
    span{
      padding-left: 45px;
    }
  }
  &__desc{
    font-weight: 700;
    font-size: 20px;
    line-height: 150%;
    max-width: 558px;
  }
  &__bottom{
    margin-top: 34px;
  }
  &__img{
    img{
      width: 100%;
      display: block;
      border-radius: 10px;
    }
  }
  &__text{
    font-size: 16px;
    line-height: 150%;
    color: #8E8E8E;
  }
  &__content{
    display: flex;
    flex-direction: column;
  }
  &__items{
    padding-top: 30px;
    margin-top: auto;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 35px 10px;
  }
  &__item{
    &-num{
      font-size: 42px;
      line-height: 120%;
      color: $primary;
      font-family: $font2;
    }
    &-title{
      font-size: 16px;
      line-height: 150%;
      margin-top: 10px;
    }
  }
}

.offer,
.feedback{
  padding: 60px 0;
  &__slider{
    margin-top: 60px;
  }
  &__nav{
    margin-top: 70px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .swiper{
    &-container{
      overflow: hidden;
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
    }
    &-button{
      &-prev,
      &-next{
        flex-shrink: 0;
        position: static;
        margin: 0 30px;
        width: 70px;
        height: 70px;
        padding: 0;
        border-radius: 50%;
        opacity: 1 !important;
        &.swiper-button-disabled{
          background-color: rgba($textColor,.1);
          path{
            fill:$textColor;
          }
        }
      }
    }
    &-pagination{
      position: static;
      width: unset;
      &-title{
        font-variant-numeric: tabular-nums;
        -webkit-font-feature-settings: "tnum";
        font-feature-settings: "tnum";
      }
    }
  }
}

.feedback{
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__title{
    color: $primary;
    span{
      color: $textColor;
    }
  }
  &__desc{
    max-width: 275px;
  }
}

.who{
  padding: 100px 0;
  margin: 60px 0;
  border-radius: 10px;
  background: linear-gradient(270deg, rgba(20, 20, 20, 0.4) 28.51%, rgba(20, 20, 20, 0) 100%), linear-gradient(0deg, rgba(20, 20, 20, 0.5), rgba(20, 20, 20, 0.5)), url(../img/who.webp);
  background-size: cover;
  background-position: center;
  position: relative;
  color: #fff;
  &__rounds{
    position: absolute;
    left: 0;
    top: 0;
  }
  &__inner{
    position: relative;
    z-index: 2;
    display: flex;
    justify-content: space-between;
  }
  &__title{
    span{
      padding-left: 50px;
    }
  }
  &__img{
    margin-top: 79px;
    display: block;
    width: 270px;
    max-width: 100%;
  }
  &__items{
    width: 570px;
    max-width: 100%;
  }
  &__item{
    padding: 30px 0;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 10px;
    border-bottom: 1px solid rgba(#fff,.3);
    &-title{
      font-weight: 700;
      font-size: 18px;
      line-height: 150%;
      text-transform: uppercase;
    }
    &-desc{
      font-size: 14px;
      line-height: 150%;
    }
    &:first-child{
      padding-top: 0;
    }
    &:last-child{
      padding-bottom: 0;
      border: none;
    }
  }
}

.team{
  padding: 60px 0;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__desc{
    max-width: 367px;
    font-size: 16px;
    line-height: 150%;
  }
  &__img{
    margin-top: 52px;
    img{
      width: 100%;
      border-radius: 10px;
    }
    &-mob{
      display: none;
    }
  }
  &__items{
    margin-top: -41px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
  }
  &__item{
    margin: 16px 15px 0;
    background: #FFFFFF;
    box-shadow: 0px 4px 44px rgba(0, 0, 0, 0.05);
    border-radius: 10px;
    width: 219px;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 700;
    font-size: 16px;
    color: #2C2E3E;
  }
}

.questions {
  padding: 60px 0;
  &__inner {
    position: relative;
    z-index: 3;
  }
  &__title {
    max-width: 315px;
    span {
      display: inline-block;
      padding-left: 85px;
      color: $textColor;
    }
  }
  &__items {
    margin-top: 60px;
  }
  &__item {
    background: #fff;
    border-radius: 10px;
    &:not(:first-child) {
      margin-top: 20px;
    }
    &-show {
      display: flex;
      justify-content: space-between;
      align-items: center;
      cursor: pointer;
      padding: 12px 12px 12px 37px;
      &--active {
        .questions__item-btn {
          img{
            transform: rotate(-90deg);
          }
        }
      }
    }
    &-btn {
      width: 45px;
      height: 45px;
      border-radius: 6px;
      margin-left: 10px;
      padding: 0;
      flex-shrink: 0;
      img{
        transition: all .3s;
      }
    }
    &-hidden {
      padding: 9px 100px 30px 70px;
      display: none;
    }
    &-text {
      opacity: 0.5;
    }
  }
}

.consultation {
  position: relative;
  padding: 60px 0;
  color: #fff;
  &__inner {
    background: linear-gradient(0deg, rgba(20, 20, 20, 0.5), rgba(20, 20, 20, 0.5)), url(../img/consultation-bg.webp);
    background-size: cover;
    background-position: center;
    position: relative;
    overflow: hidden;
    padding: 99px 100px 95px;
    border-radius: 10px;
  }
  &__img{
    position: absolute;
    right: 0;
    top: 0;
    pointer-events: none;
  }
  &__top {
    display: flex;
    justify-content: space-between;
  }
  &__title {
    max-width: 440px;
    span {
      display: inline-block;
      padding-left: 36px;
    }
  }
  &__desc {
    max-width: 500px;
  }
  @media screen and (min-width: 992px){
    &__grid {
      margin-top: 70px !important;
    }
  }
}

.map{
  height: 498px;
  width: 100%;
  margin-top: 60px;
  iframe{
    width: 100%;
    height: 100%;
  }
}


@media screen and (max-width: 1150px) {
  .consultation {
    &__top {
      display: block;
    }
    &__desc {
      margin-top: 30px;
    }
  }
}



@media screen and (max-width: 992px) {
  .main{
   margin-top: -63px;
   &__inner{
     padding: 100px 0 80px;
     grid-template-columns: 100%;
     gap: 29px;
   }
   &__desc2{
     max-width: 150px;
   }
   &__desc{
     max-width: 172px;
   }
   &__title{
     max-width: 280px;
   }
   &__bottom{
     margin-top: 30px;
     display: block;
   }
   &__btn{
     width: 132px;
     height: 132px;
     margin: 12px 0 0 auto;
     font-size: 10px;
   }
   &__img{
     width: 140px;
   }
 }

  .advantages{
    margin-top: 30px;
    padding: 30px 0;
    &__items{
      gap: 16px;
      margin-top: 30px;
    }
  }

  .facts{
    padding: 30px 0;
    &__grid{
      grid-template-columns: 100%;
    }
    &__bottom{
      margin-top: 30px;
    }
    &__tree{
      top: 10px;
      &--mob{
        display: block;
      }
      &--desktop{
        display: none;
      }
    }
    &__desc{
      font-size: 14px;
    }
    &__text{
      font-size: 12px;
    }
    &__items{
      grid-template-columns: 1fr 1fr 1fr;
    }
    &__item{
      &-num{
        font-size: 20px;
      }
      &-title{
        font-size: 12px;
        margin: 0;
      }
    }
  }

  .offer,
  .feedback{
    padding: 30px 0;
    overflow: hidden;
    &__title{
      text-align: center;
    }
    &__slider{
      margin-top: 30px;
    }
    .swiper{
      &-container{
        overflow: unset;
      }
      &-slide{
        width: 280px;
        max-width: calc(100% - 5px);
      }
      &-button{
        &-prev,
        &-next{
          margin: 0 12px;
          width: 45px;
          height: 45px;
          svg{
            width: 18px;
            height: auto;
          }
        }
      }
    }
    &__nav{
      margin-top: 30px;
    }
  }

  .feedback{
    &__top{
      display: block;
    }
    &__title{
      text-align: left;
      span{
        padding-left: 25px;
      }
    }
    &__desc{
      margin-top: 30px;
      font-size: 12px;
      br{
        display: none;
      }
    }
  }

  .who{
    padding: 60px 0;
    margin: 30px 0;
    &__rounds{
      width: 152px;
    }
    &__inner{
      display: block;
    }
    &__left{
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    &__title{
      span{
        padding-left: 24px;
      }
    }
    &__img{
      width: 117px;
      margin: 0 0 0 10px;
    }
    &__right{
      margin-top: 26px;
    }
    &__items{
      width: 100%;
    }
    &__item{
      padding: 20px 0;
      grid-template-columns: 100%;
      &-title{
        font-size: 14px;
      }
      &-desc{
        font-size: 12px;
      }
    }
  }

  .team{
    padding: 30px 0;
    &__top{
      display: block;
    }
    &__desc{
      margin-top: 30px;
    }
    &__img{
      margin-top: 30px;
    }
    &__items{
      margin-top: -38px;
    }
    &__item{
      width: 240px;
      height: 44px;
      max-width: 100%;
      font-size: 12px;
    }
  }

  .questions {
    padding: 30px 0;
    &__title {
      font-size: 20px;
      max-width: 150px;
      span {
        padding-left: 45px;
      }
    }
    &__items {
      margin-top: 30px;
    }
    &__item {
      &:not(:first-child) {
        margin-top: 15px;
      }
      &-show {
        padding: 30px;
      }
      &-btn {
        width: 26px;
        height: 26px;
        img {
          width: 14px;
          height: auto;
        }
      }
      &-title{
        font-size: 12px;
      }
    }
  }

  .consultation{
    padding: 30px 0;
    &__inner{
      padding: 30px;
    }
    &__title{
      font-size: 20px;
      span:last-child{
        padding-left: 24px;
      }
    }
    &__img{
      width: 200px;
    }
  }

  .map{
    margin-top: 30px;
  }
}


@media screen and (max-width: 768px){
  .advantages{
    &__items{
      grid-template-columns: 1fr 1fr;
    }
  }
}


@media screen and (max-width: 576px){
  .advantages{
    &__items{
      grid-template-columns: 100%;
    }
  }

  .team{
    &__img{
      &-mob{
        display: block;
      }
      &-desktop{
        display: none;
      }
    }
  }
}
