.footer{
  color: #fff;
  background: $textColor;
  border-radius: 10px 10px 0px 0px;
  padding: 112px 0;
  &__grid{
    display: grid;
    grid-template-columns: 2.1fr 1.6fr .8fr 1.4fr;
    gap: 10px;
  }
  &__link{
    display: block;
    font-size: 14px;
    line-height: 35px;
  }
  &__btn{
    width: 100%;
    height: 53px;
  }
  &__bottom{
    margin-top: 67px;
  }
  &__socials{
    display: flex;
    align-items: center;
    justify-content: space-between;
    &-item{
      &:hover{
        svg{
          transform: translateY(-10px);
        }
      }
    }
  }
  .lang{
    &__second{
      color: #fff;
      opacity: 1;
    }
    &__first{
      color: $primary;
    }
    input{
      &:checked{
        &~.lang__first{
          opacity: 1;
          color: #fff;
        }
        &~.lang__second{
          color: $primary;
        }
      }
    }
    label{
      &:before{
        background-color: $primary;
      }
    }
  }
}


@media screen and (max-width: 992px) {
  .footer{
    padding: 70px 0 30px;
    &__top{
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 30px 25px;
      position: relative;
      .footer__col{
        &:nth-child(1){
          grid-column: 1/3;
        }
      }
    }
    &__link{
      font-size: 12px;
      line-height: 250%;
    }
    &__bottom{
      display: flex;
      flex-direction: column;
      gap: unset;
      margin-top: 30px;
    }
    &__lang{
      order: 1;
    }
    &__socials{
      order: 2;
      margin-top: 30px;
    }
    &__privacy{
      order: 3;
      margin-top: 133px;
      text-align: center;
    }
    &__conf{
      order: 4;
      margin-top: 10px;
      text-align: center;
    }
    &__privacy,
    &__conf{
      font-size: 12px;
      line-height: 150%;
    }
    &__btn{
      position: absolute;
      left: 0;
      top: 100%;
      height: 44px;
      margin-top: 140px;
    }
  }
}
