.offer-card{
  background: #FFFFFF;
  border-radius: 10px;
  padding: 35px;
  display: flex;
  flex-direction: column;
  &__top{
    display: flex;
    align-items: center;
  }
  &__icon{
    width: 80px;
    height: 80px;
    flex-shrink: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 10px;
    background-color: $primary;
    margin-right: 20px;
  }
  &__title{
    font-weight: 700;
    font-size: 18px;
    line-height: 150%;
  }
  &__list{
    display: grid;
    grid-template-columns: 100%;
    gap: 10px;
    margin-top: 20px;
    li{
      font-size: 14px;
      line-height: 150%;
      display: flex;
      &:before{
        content: '';
        width: 3px;
        height: 3px;
        display: block;
        border-radius: 50%;
        background-color: $textColor;
        flex-shrink: 0;
        margin: 0 9px;
        transform: translateY(8px);
      }
    }
  }
  &__bottom{
    padding-top: 72px;
    margin-top: auto;
  }
  &__period{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 20px 16px;
    background-color: $bg;
    border-radius: 10px;
    font-size: 14px;
    line-height: 150%;
    &-title{
      font-weight: 700;
    }
    &-desc{
      color: $primary;
      margin-left: 5px;
    }
  }
  &__price{
    font-size: 18px;
    line-height: 150%;
    font-weight: 700;
    color: $primary;
    margin-top: 20px;
    text-align: center;
  }
  &__btn{
    width: 100%;
    margin-top: 20px;
    border-radius: 10px;
    font-weight: 600;
  }
}

.feedback-card{
  padding: 35px;
  background: #FFFFFF;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  &__top{
    display: flex;
    align-items: center;
  }
  &__img{
    flex-shrink: 0;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    margin-right: 20px;
    background-color: #D9D9D9;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__text{
    margin: 20px 0;
    font-weight: 500;
  }
  &__link{
    font-size: 16px;
    line-height: 150%;
    margin-top: auto;
    font-weight: 700;
    color: $primary;
    &:hover{
      color: $textColor;
    }
  }
}

.advantage-card{
  padding: 22px 35px 35px;
  background-color: #fff;
  border-radius: 10px;
  &__num{
    font-family: $font2;
    font-size: 52px;
    line-height: 150%;
    opacity: .1;
  }
  &__icon{
    display: block;
    margin-top: -27px;
  }
  &__title{
    font-weight: 700;
    font-size: 18px;
    line-height: 150%;
    margin-top: 27px;
  }
  &__desc{
    margin-top: 10px;
  }
}


@media screen and (max-width: 992px){
  .offer-card{
    padding: 30px;
    &__icon{
      width: 60px;
      height: 60px;
    }
    &__title{
      font-size: 14px;
    }
    &__list{
      li{
        font-size: 12px;
      }
    }
    &__bottom{
      padding-top: 20px;
    }
    &__period{
      font-size: 10px;
      padding: 14px;
    }
    &__price{
      font-size: 14px;
    }
    &__btn{
      height: 44px;
      font-size: 12px;
    }
  }

  .feedback-card{
    padding: 30px;
    &__img{
      margin-right: 16px;
      width: 50px;
      height: 50px;
    }
    &__name{
      font-size: 14px;
    }
    &__text{
      font-size: 12px;
      margin: 16px 0;
    }
    &__link{
      font-size: 12px;
    }
  }

  .advantage-card{
    padding: 30px 30px 35px;
    &__num{
      font-size: 28px;
    }
    &__icon{
      width: 22px;
      margin-top: -15px;
    }
    &__title{
      font-size: 14px;
      margin-top: 16px;
    }
    &__desc{
      font-size: 12px;
      margin-top: 8px;
    }
  }
}
