.logo{
  display: block;
  width: 136px;
  flex-shrink: 0;
  img{
    width: 100%;
    display: block;
  }
}

.header{
  color: #fff;
  padding: 30px 0;
  position: relative;
  z-index: 5;
  width: 100%;
  left: 0;
  top: 0;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__mob{
    flex-grow: 1;
    display: flex;
    align-items: center;
    justify-content: space-between;
    &:before{
      content: '';
      display: block;
      width: 1px;
    }
  }
  &__burger{
    display: none;
  }
  &__lang{
    flex-shrink: 0;
  }
  &__socials{
    display: flex;
    align-items: center;
    &--mob{
      display: none;
    }
  }
  &__social{
    width: 29px;
    height: 29px;
    background: rgba(255, 255, 255, 0.2);
    border-radius: 4px;
    display: flex;
    align-items: center;
    justify-content: center;
    &:not(:last-child){
      margin-right: 10px;
    }
  }
  &__tel{
    font-weight: 700;
    font-size: 14px;
    line-height: 150%;
  }
  &--fix{
    background: rgba($textColor, .9);
    position: fixed;
  }
}

.nav{
  display: flex;
  align-items: center;
  &__link{
    font-size: 14px;
    line-height: 150%;
    &:not(:last-child){
      margin-right: 30px;
    }
  }
}

.lang{
  display: flex;
  align-items: center;
  input{
    position: absolute;
    opacity: 0;
    pointer-events: none;
    &:checked{
      &+label{
        &:before{
          left: 12px;
        }
      }
      &~.lang__first{
        opacity: 0.6;
      }
      &~.lang__second{
        color: #fff;
        opacity: 1;
      }
    }
  }
  label{
    background: rgba(255, 255, 255, 0.2);
    border-radius: 40px;
    width: 28px;
    height: 16px;
    flex-shrink: 0;
    margin: 0 8px;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      width: 12px;
      height: 12px;
      background-color: #fff;
      left: 2px;
      top: 2px;
      border-radius: 50%;
      transition: all .3s;
    }
  }
  &__item{
    font-size: 14px;
    line-height: 150%;
    text-transform: uppercase;
    transition: all .3s;
  }
  &__first{
    order: -1;
  }
  &__second{
    opacity: 0.6;
  }
}


@media screen and (max-width: 992px) {
  .logo{
    width: 80px;
  }

  .header{
    padding: 17px 0;
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: -100%;
      height: 100%;
      width: 100%;
      background: $bg;
      z-index: 21;
      transition: all .6s;
    }
    &__mob{
      z-index: 20;
      flex-direction: column;
      align-items: unset;
      justify-content: unset;
      background: $bg;
      position: fixed;
      top: 0;
      left: -100%;
      width: 100%;
      height: 100vh;
      transition: all .6s;
      margin: 0;
      padding: 75px 20px 50px;
      overflow-y: auto;

    }
    &__logo{
      z-index: 22;
      position: relative;
    }
    &__burger{
      width: 40px;
      display: block;
      z-index: 22;
      position: relative;
      span{
        border-radius: 1px;
        height: 2px;
        width: 100%;
        display: block;
        background-color: $primary;
        transition: all .5s;
        &:not(:last-child){
          margin-bottom: 8px;
        }
      }

    }
    &__lang{
      margin: 40px 0;
    }
    &__socials{
      position: relative;
      &--desktop{
        display: none;
      }
      &--mob{
        display: flex;
      }
      z-index: 22;
    }
    &__social{
      margin: 0 5px;
    }
    &._open{
      &:before{
        left: 0;
      }
      .header__burger{
        span{
          &:nth-child(1){
            transform: rotate(-45deg) translateY(7px);
          }
          &:nth-child(2){
            transform: rotate(45deg) translateY(-7px);
          }
        }

      }
      .header__mob{
        left: 0;
      }
    }
  }

  .nav{
    flex-direction: column;
    &__link{
      font-size: 20px;
      margin-right: auto !important;
      &:not(:last-child){
        margin-bottom: 15px;
      }
    }
  }
}
