.consult{
  &__grid{
    margin-top: 50px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 30px;
    &--m0{
      margin: 0;
    }
  }
  &__form,
  &__info{
    display: grid;
    grid-template-columns: 100%;
    gap: 30px;
  }
}


@media screen and (max-width: 992px){
  .consult{
    &__grid{
      margin-top: 30px;
      grid-template-columns: 100%;
      gap: 16px;
    }
    &__form,
    &__info{
      gap: 16px;
    }
  }
}

